<template>

	<section class="index-brand">
        <div class="index-brand-in">
            <ul class="clearfix" :class='{"index-brand-info":look}'>
                <li v-for="(item,index) in brandList" :brand=item.id @click="goBrand(item.id,item.name)">
                    <img :src=item.logoUrl >
                    <span>{{item.name}}</span>
                </li>
            </ul>
            <p class="index-more-brand" @click="seeMore" v-if="look"> <i class="yellow-bt"></i></p>
            <p class="index-more-brand brand-collect" @click="seeMore" v-if="collect"><i class="yellow-bt"></i></p>
        </div>
    </section>

</template>

<script>
export default {
  name: 'brand',
  props:["brandList"],
  data () {
    return {
      brandLen:5,
      look:false,
      collect:false
    }
  },
  methods:{  
  	seeMore(){ // 
  		this.look = !this.look;
  		this.collect = !this.collect
  	},
  	goBrand(index,name){ //
  		this.$router.push('brand/' + index); //
      this.$store.dispatch("DEFAULT_BRAND", // store
        name
      );
  	}
  },
  mounted(){
  	this.look = this.brandList.length>this.brandLen?true:false;
  }
}
</script>
<style>
/**/
.index-brand{padding:.5333rem 0.4rem;}
.index-brand-in{padding-bottom:.5333rem;background-color:#fff;}
.index-brand-in li{float:left;width:20%;text-align:center;overflow:hidden;}
.index-brand-in li img{display:block;width:1.067rem;height:1.067rem;margin:.533rem auto 0;}
.index-brand-in li span{display:block;line-height:1;color:#2c2c2c;font-size:0.3733rem;white-space:nowrap;margin-top:0.133rem;}
.index-more-brand{margin-top:.8rem;text-align:center;color:#d5aa5c;font-size:0.373rem;}
.index-more-brand i{margin-left:.1333rem;position:relative;top:-0.04rem;}
.brand-collect i{transform:rotateX(180deg)}

.index-brand-info{
  height:2.1rem;
  overflow:hidden;
}
</style>


